import Vue from "vue";
import Router from "vue-router";

Vue.use(Router);

const globalRouters = [
  {
    name: "login",
    path: "/login",
    component: () => import("@/views/common/Login"),
    meta: {
      title: "登陆",
      isGlobal: true
    },
    beforeEnter(to, from, next) {
      const token = Vue.cookie.get("token");
      token && /\S/.test(token) ? next({ name: "order" }) : next();
    }
  },
  {
    name: "forget",
    path: "/forget",
    component: () => import("@/views/common/Forget"),
    meta: {
      title: "忘记密码",
      isGlobal: true
    },
    beforeEnter(to, from, next) {
      const token = Vue.cookie.get("token");
      token && /\S/.test(token) ? next({ name: "order" }) : next();
    }
  }
]

const mainRoute = {
  path: "/",
  name: "root",
  redirect: { name: "login" },
  component: () => import("@/views/layout"),
  children: [
    {
      name: "order",
      path: "/order",
      component: () => import("@/views/modules/order/index.vue"),
      meta: {
        title: "商家订单列表"
      }
    },
    {
      name: "withdraw",
      path: "/withdraw",
      component: () => import("@/views/modules/withdraw/index.vue"),
      meta: {
        title: "提现地址"
      }
    },
    {
      name: "wallet",
      path: "/wallet",
      component: () => import("@/views/modules/wallet/index.vue"),
      meta: {
        title: "钱包"
      }
    },
    {
      name: "user",
      path: "/user",
      component: () => import("@/views/modules/user/index.vue"),
      meta: {
        title: "用户信息"
      }
    },
    {
      name: "password",
      path: "/password",
      component: () => import("@/views/modules/password/index.vue"),
      meta: {
        title: "修改密码"
      }
    }
  ]
};

const router = new Router({
  mode: "hash",
  scrollBehavior: () => ({ y: 0 }),
  routes: [...globalRouters, mainRoute]
})

router.beforeEach((to, from, next) => {
  const token = Vue.cookie.get("token");
  const hasToken = token && /\S/.test(token);

  // 1. 未登陆
  if (!hasToken) {
    // - 是全局路由, 直接访问
    // - 否则, 重定向至 { name: 'login' }
    return isGlobalRoute(to) ? next() : next({ name: "login" });
  } else {
    // 2. 已登陆
    next();
  }
});

/**
 * 判断当前路由是否为全局路由
 * @param {Router} route 路由对象
 */
function isGlobalRoute(route = { meta: { isGlobal: false } }) {
  const { meta: { isGlobal = false } = {} } = route;
  return isGlobal;
}

export default router;